<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card>
            <template slot="header">
              <div class="row">
                <div class="col-8">
                  <h4 class="card-title">All Portfolios</h4>
                  <p class="card-category">Filter attributes to see desired portfolios</p>
                </div>
                <div class="col-4">
                </div>
              </div>
            </template>
            <vue-good-table
              :columns="columnsPortfolio"
              :paginate="true"
              :rows="rowsPortfolio"
              :globalSearch = "false"
              styleClass="table table-bordered table-striped">
              <template slot="table-column" slot-scope="props">
                {{props.column.label}}
              </template>
            </vue-good-table>
          </card>
          <card>
            <template slot="header">
              <div class="row">
                <div class="col-8">
                  <h4 class="card-title">All Projects</h4>
                  <p class="card-category">Select desired columns to display in project report</p>
                  <div class="row">
                    <div class="col-8">
                      <multiselect v-model="selectedProjectColumns"
                                   placeholder="Pick a column(s)"
                                   label="name"
                                   track-by="name"
                                   :options="columnFilterNames"
                                   :multiple="true"></multiselect>
                    </div>
                    <div class="col-2">
                        <button type="submit" id="projectColumnFilterSubmit" class="btn btn-info btn-fill float-left" style="margin-right: 5px;" @click="selectProjectColumns">Apply</button>
                    </div>
                    <div class="col-2">
                      <button type="submit" id="projectColumnSelectAll" class="btn btn-info btn-fill float-left" style="margin-right: 5px;" @click="selectAllProjectColumns">Show All</button>
                    </div>
                  </div>
                  <pre class="language-json"></pre>
                </div>
                <div class="col-4">
                </div>
              </div>
            </template>
            <vue-good-table
              :columns="columnsProject"
              :paginate="true"
              :rows="rowsProject"
              :globalSearch = "false"
              styleClass="table table-bordered table-striped">
              <template slot="table-column" slot-scope="props">
                    {{props.column.label}}
              </template>
            </vue-good-table>
          </card>
          <card>
            <template slot="header">
              <div class="row">
                <div class="col-8">
                  <h4 class="card-title">All Resources</h4>
                  <p class="card-category">Filter resources to see desired report</p>
                </div>
                <div class="col-4">
                </div>
              </div>
            </template>
            <vue-good-table
              :columns="columnsResource"
              :paginate="true"
              :rows="rowsResource"
              :globalSearch = "false"
              styleClass="table table-bordered table-striped">
              <template slot="table-column" slot-scope="props">
                {{props.column.label}}
              </template>
            </vue-good-table>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import Multiselect from 'vue-multiselect'
  import axios from 'axios'

  const tableColumns = ['Name', 'ProjectStatus', 'Manager', 'ProjectOwner', 'Status', 'ProjectResources', 'Budget', 'Budget Used'];
  // 'Start Date', 'End Date'

  import Vue from 'vue';
  import VueGoodTable from 'vue-good-table';

  Vue.use(VueGoodTable);

  export default {
    components: {
      Card,
      Multiselect
    },

    name: 'Checkbox-table',
    created () {
      this.fetchData();
    },
    status: 'Checkbox-table',
    created () {
      this.fetchData();
    },
    rag_status: 'Checkbox-table',
    created () {
      this.fetchData();
    },
    businessOwner: 'Checkbox-table',
    created () {
      this.fetchData();
    },

    mounted: function(){
      this.initProjectColumnMap();
    },


    data () {
      return {
        selectedProjectColumns: [],
        projectColumnsMap: new Map(),
        columnFilterNames: [
          {name: "Name"},
          {name: "Project Status"},
          {name: "Manager"},
          {name: "Project Owner"},
          {name: "RAG Status"},
          {name: "Number of resources"},
          {name: "Budget"},
          {name: "Budget used"}
        ],

        columnsPortfolio: [
          {
            label: 'ID',
            field: 'id',
            filterable: true,
          },
          {
            label: 'Classification',
            field: 'classification',
            type: 'string',
            filterable: true,
          },
          {
            label: 'Business Owner',
            field: 'businessOwner',
            filterable: true,
          },
          {
            label: 'Resource Breakdown',
            field: 'resourceBreakdown',
            filterable: true,
          },
        ],
        columnsProject: [
          {
            label: 'Name',
            field: 'name',
            filterable: true,
          },
          {
            label: 'Project Status',
            field: 'status',
            filterable: true,
          },
          {
            label: 'Manager',
            field: 'manager',
            type: 'string',
            filterable: true,
          },
          {
            label: 'Project Owner',
            field: 'businessOwner',
            filterable: true,
          },
          {
            label: 'RAG Status',
            field: 'rag_status',
            filterable: true,
          },
          {
            label: 'Number of Resources',
            field: 'projectResourses',
            filterable: true,
          },
          {
            label: 'Budget',
            field: 'budget',
            filterable: true,
          },
          {
            label: 'Budget Used',
            field: 'budget_used',
            filterable: true,
          }
          // {
          //   label: 'Start Date',
          //   field: 'start_date',
          //   filterable: true,
          // },
          // {
          //   label: 'End Date',
          //   field: 'end_date',
          //   filterable: true,
          // },
        ],
        columnsResource: [
          {
            label: 'Name',
            field: 'name',
            filterable: true,
          },
          {
            label: 'Email',
            field: 'email',
            type: 'string',
            filterable: true,
          },
          {
            label: 'Location',
            field: 'location',
            filterable: true,
          },
          {
            label: 'Group',
            field: 'group',
            filterable: true,
          },
        ],
        rowsPortfolio: [],
        rowsProject: [],
        rowsResource: []
      };
    },
    methods: {
      fetchData() {
        let info = this;
        axios.get(this.$root.serverURL + "/api/portfolios")
          .then(response => {
            info.rowsPortfolio = response.data;
            for (let i = 0; i < info.rowsPortfolio.length; i++){
               info.rowsPortfolio[i].businessOwner = info.rowsPortfolio[i].businessOwner.name;
               info.rowsPortfolio[i].classification = info.rowsPortfolio[i].classification.name;
            }
            console.log(response.data);
          });

        axios.get(this.$root.serverURL + "/api/projects")
          .then(response => {
            info.rowsProject = response.data;
            console.log(response.data);
            for (let i = 0; i < info.rowsProject.length; i++) {
              info.rowsProject[i].manager = info.rowsProject[i].manager.name;
              info.rowsProject[i].status = info.rowsProject[i].status.name;
            }
          });
        axios.get(this.$root.serverURL + "/api/resources")
          .then(response => {
            info.rowsResource = response.data;
          })
      },

      initProjectColumnMap(){
        for(var i = 0; i<this.columnsProject.length; i++){
          this.projectColumnsMap.set(this.columnsProject[i].label, this.columnsProject[i]);
        }
      },

      selectProjectColumns(){
        var columnsToDisplay = this.selectedProjectColumns;
        if(this.selectedProjectColumns.length > 0){
          this.columnsProject = [];
          for (var i=0; i<columnsToDisplay.length; i++){
            this.columnsProject.push(this.projectColumnsMap.get(columnsToDisplay[i].name));
          }
        }
      },

      selectAllProjectColumns() {
        var iterator = this.projectColumnsMap.entries();
        var entry = iterator.next();
        if(!entry.done){
          this.columnsProject = [];
        }
        while(!entry.done){
          this.columnsProject.push(entry.value[1]);
          entry = iterator.next();
        }
      },
    }
  }
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css">
</style>




