<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="filters">
          <span>Search: </span>
          <input v-model="searchKeyword" v-on:input="filterPortfolios" />
        </div>
      </div>
      <div class="row">
          <portfolio-card style="margin: 0.5%;box-shadow: 5px 5px 5px grey;"
            v-for="portfolio of portfoliosDisplayed"
            v-bind:key="portfolio.id"
            v-bind="portfolio">
          </portfolio-card>

        <add-portfolio-card style="margin: 0.5%;box-shadow: 5px 5px 5px grey;cursor:pointer;"></add-portfolio-card>

      </div>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import LTable from 'src/components/UIComponents/Table.vue'
  import Checkbox from 'src/components/UIComponents/Inputs/Checkbox.vue'
  import PortfolioCard from 'src/components/UIComponents/Cards/PortfolioCard.vue'
  import AddProjectCard from 'src/components/UIComponents/Cards/AddProjectCard.vue'
  import AddPortfolioCard from 'src/components/UIComponents/Cards/AddPortfolioCard.vue'
  import _ from 'lodash'
  import axios from 'axios'

  export default {
    components: {
      Checkbox,
      Card,
      LTable,
      PortfolioCard,
      AddProjectCard,
      AddPortfolioCard
    },
    created() {
      this.fetchData();
    },
    data() {
      return {
        portfolios: [],
        portfoliosDisplayed: [],
        // portfolios: [
        //   {
        //     classification: 'Top Secret',
        //     businessOwner: 'Mark Penderson',
        //     numProjects: 52,
        //     totalBudget: 800000
        //   },
        //   {
        //     classification: 'Sheep hunting operations',
        //     businessOwner: 'Nigel',
        //     numProjects: 8,
        //     totalBudget: 1000000
        //   },
        //   {
        //     classification: 'Financials',
        //     businessOwner: 'John Doe',
        //     numProjects: 29,
        //     totalBudget: 550000
        //   }
        // ],
        // portfoliosDisplayed: [
        //   {
        //     classification: 'Top Secret',
        //     businessOwner: 'Mark Penderson',
        //     numProjects: 52,
        //     totalBudget: 800000
        //   },
        //   {
        //     classification: 'Sheep hunting operations',
        //     businessOwner: 'Nigel',
        //     numProjects: 8,
        //     totalBudget: 1000000
        //   },
        //   {
        //     classification: 'Financials',
        //     businessOwner: 'John Doe',
        //     numProjects: 29,
        //     totalBudget: 550000
        //   }
        // ],
        searchKeyword: ''
      }
    },
    methods: {
      fetchData() {
        var info = this;
        axios.get(this.$root.serverURL + "/api/portfolios")
          .then(response => {
            info.portfolios = response.data;
            for (var p of info.portfolios){
              p.numProjects = p.projects.length;
            }
            info.portfoliosDisplayed = info.portfolios.slice();
            // console.log('portfolios:', info.portfolios);
          })
      },

      filterPortfolios: _.debounce(function () {
        this.portfoliosDisplayed = [];
        for(var aPortfolio of this.portfolios) {
            if(aPortfolio.classification.toLowerCase().indexOf(this.searchKeyword.toLowerCase())!=-1) {
                this.portfoliosDisplayed.push(aPortfolio);
            }
        }
      }, 500)

    }
  }
</script>
<style scoped>
  div.filters {
    text-align: center;
    width: 100%;
    padding: 1em;
    border: 1px solid grey;
    border-radius: 2px;
    margin-bottom: 1em;
    color: white;
    background-color: grey;
  }
</style>



